import './sass/main.scss';
import { getRefs } from './js/refs'
import { debounce } from 'throttle-debounce';
import { consts } from "./js/consts";
import { onFilmClick } from "./js/sectionFilmoteka"
import { changeHeader } from "./js/changeHeader"
import { onPaginationNavClick } from "./js/paginationNav"
import { onDevLinkClick } from "./js/footerDevelopers"
import { onLoad } from "./js/onLoad"
import { activateScrollButton } from './js/btnScrollUp'
import './js/theme';


const refs = getRefs();
onLoad(refs); //       
              //        
console.log("  ");
console.dir(refs); //     .    


//    
refs.filmsSection.addEventListener('click', e => {
    e.preventDefault;
    onFilmClick(e); //from "./js/sectionFilmoteka"     
});

//    header nav
refs.headerNav.addEventListener('click', e => {
  e.preventDefault;
  if (e.target.nodeName === "A") {
    changeHeader(e.target.textContent, refs.headerDivToChange); //from "./js/headerNav"   HOME/MY LIBRARY
  };
});

//    header logo
refs.headerLogo.addEventListener('click', e => {
  changeHeader('HOME', refs.headerDivToChange); //from "./js/headerNav"     HOME
});

//    header pagination nav
refs.paginationNav.addEventListener('click', e => {
  e.preventDefault;
  if (e.target.nodeName === "A") { onPaginationNavClick(e.target.textContent) };
});

//    header developer link
refs.devLink.addEventListener('click', e => {
    e.preventDefault;
    onDevLinkClick(e); //from "./js/footerDevelopers"     
});

//    scroll UP
window.addEventListener('scroll',
  debounce(consts.DEBOUNCE_DELAY, (e) => {
     activateScrollButton(e);//from "./js/btnScrollUp"       
   }));






