import { getRefs } from './refs';
import {addClass, removeClass } from './classWork'

const refs = getRefs();

const scrollToTopOfPage = () => { //    
  if (window.pageYOffset > 0) {
    window.scrollTo( 0, -80 );
  }
}

export const activateScrollButton = (e) => { //    
  const coords = document.documentElement.clientHeight;
  if (window.pageYOffset > coords) {
    addClass(refs.scrollTopBtn, 'active-scroll');
    refs.scrollTopBtn.addEventListener('click', e => {//   
      e.preventDefault;
      scrollToTopOfPage(e); 
    });
  }

  if (window.pageYOffset < coords) {
    removeClass(refs.scrollTopBtn, 'active-scroll');
    refs.scrollTopBtn.removeEventListener('click', e => {//   
      e.preventDefault;
      scrollToTopOfPage(e); 
    });
  }
}